<template>
  <div>
    <a-card style="">
      <v-row align="center" justify="center">
        <v-col class="pt-3 pt-0 pb-0" cols="9" align="left">
          <p>ปฏิทินการประชุมของท่าน</p>
        </v-col>

        <v-col class="mb-1 pt-0 pb-0" cols="3" align="right">
          <a-icon type="snippets" />
        </v-col>
      </v-row>
    </a-card>

    <v-row class="pt-3 pb-1 pt-0 pb-0">
      <v-col class="">
        <a-alert
          type="info"
          show-icon
          description="ท่านสามารถกดวันที่ในปฏิทิน เพื่อดูการประชุมของท่าน"
        />
      </v-col>
    </v-row>
    <div class="mt-2 mb-5" style="border: 1px solid #d9d9d9; border-radius: 4px">
      <a-calendar :fullscreen="false" @select="onSelect" :header-render="headerRender" @panelChange="onPanelChange">
        <!-- <ul slot="dateCellRender" slot-scope="value" class="events">
          <li v-for="item in getListData(value)" :key="item.content">
            <a-badge :status="item.type" />
          </li>
        </ul>
        <template slot="monthCellRender" slot-scope="value">
          <div v-if="getMonthData(value)" class="notes-month">
            <section>{{ getMonthData(value) }}</section>
            <span>Backlog number</span>
          </div>
        </template> -->
      </a-calendar>
    </div>
    <DialogShowMeetingPerson
      ref="DialogShowMeetingPerson"
    ></DialogShowMeetingPerson>
  </div>
</template>
<script>
import DialogShowMeetingPerson from "./DialogShowMeetingPerson.vue";
import moment from "moment";
export default {
  components: {
    DialogShowMeetingPerson,
  },
  data: () => ({
    format: "YYYY-MM-DD",
    day_event_calendar: null,
    user_login:"",
    email_user:""
  }),
  created(){
   this.getdata_storage();
  },
  methods: {
    moment,
     onSelect(value) {
      if (value != null) {
        var myJSONselectedValue = JSON.stringify(value);
        this.OpenDialogShowMeetingPerson(myJSONselectedValue);
      } else {
        this.$message.error("กรุุณาเลือกวันที่ให้ถูกต้อง");
      }
    },
    OpenDialogShowMeetingPerson(myJSONselectedValue) {
      var selectedValue = myJSONselectedValue.slice(1, 11);
      this.$refs.DialogShowMeetingPerson.open(selectedValue);
    },
    onPanelChange(value, mode) {
      // eslint-disable-next-line no-console
      console.log(value, mode);
    },
    getListData(value) {
      let listData;
      switch (value.date()) {
        case 8:
          listData = [{ type: "warning" }];
          break;
        case 10:
          listData = [{ type: "success" }];
          break;
        case 15:
          listData = [{ type: "warning" }];
          break;
        default:
      }
      return listData || [];
    },

    getMonthData(value) {
      if (value.month() === 8) {
        return 1394;
      }
    },
     API_GetBookingCalendarUser() {

      this.axios({
        method: "GET",
        url: this.$store.state.url_get_API_axios + "/api/v1/bookingTable/"+ this.email_user +"/all",
        // headers: { Authorization: "Bearer " + localStorage.token },
      })
        .then((response) => {
          console.log(response.statusText);
          console.log(response.data);
          var results =  response.data;
          for(var items in results){
               console.log(items.booking_number);
          }
        })
        .catch((error) => {
          // this.spinning = false;
          console.log(error.message);
          // this.loading = false;
          // if (error.message == "Request failed with status code 400") {
          //   this.$message.error("ไม่พบข้อมูล");
          // }
        });
    },
     headerRender({ value, onChange }) {
      const Now_Month = new Date(value);
      const Next_Month = new Date(value);

      const start = Now_Month.getMonth();
      const end = (Next_Month.getMonth() + 1);
      const end_month = end;
      const monthOptions = [];

      const current = value.clone();
      const localeData = value.localeData();
      const months = [];
      for (let i = 0; i < 12; i++) {
        current.month(i);
        months.push(localeData.monthsShort(current));
      }
      console.log(end_month);
      for (let index = start; index <= end; index++) {
        console.log(index);
        console.log(end);
        if(index <= end_month){
           monthOptions.push(
          <a-select-option class="month-item" key={`${index}`}>
            {months[index]}
          </a-select-option>,
        );
        }
       
      }
      const month = value.month();

   
      return (
             <div style={{ padding: '10px' }}>

          <a-row type="flex" justify="end ">
     
            <a-col>
              <a-select
                
                dropdownMatchSelectWidth={false}
                value={String(month)}
                onChange={selectedMonth => {
                  const newValue = value.clone();
                  newValue.month(parseInt(selectedMonth, 10));
                  onChange(newValue);
                }}
              >
                {monthOptions}
              </a-select>
            </a-col>
          </a-row>
        </div>
      );
    },
        getdata_storage() {
      let data = JSON.parse(window.localStorage.getItem("user_data"));

      if (data === null) {
        this.getdata = null;
      } else {
        this.email_user = data.email;
      }
    },




  },
};
</script>
<style scoped>
.events {
  list-style: none;
  margin: 0;
  padding: 0;
}
.events .ant-badge-status {
  overflow: hidden;
  white-space: nowrap;
  width: 100%;
  text-overflow: ellipsis;
  font-size: 12px;
}
.notes-month {
  text-align: center;
  font-size: 28px;
}
.notes-month section {
  font-size: 28px;
}
</style>
